@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
}
.main-modal{
    height: 100%;
    width: 100%;
}
.grid{
    display: grid;
    height: 100%;
    width: 100%;
    color: #fdeed0;
    text-align: justify;
    background-color: #000000c2;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 3fr 1fr 1fr 1fr;
    font-family: 'Montserrat';
}
.image-profile{
    padding: 5px;
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
    background-color: #7050114b;
    border: 3px inset #ffffff77;
    align-self: center;
    justify-self: center;
    grid-column: 1;
    grid-row: 1;
}
.content-modal{
    display: grid;
    grid-column: 2/4;
    grid-row: 1/5;
    grid-template-rows: repeat(2, 1fr);
}
.about-champion-modal{
    grid-row: 1;
}
.descrip-profile{
    background-color: #f3cdcd42;
    padding: 0rem 1rem 0rem 1rem;
    box-shadow: 2px 3px 4px #d3902542;
    border: 1px solid wheat;
    margin: 20px;
}
.skill-profile{
    grid-row: 2;
    margin: auto 20px;
}
.skill-profile img{
    width: 12%;
    border: 2px outset #fbca5f;
    box-shadow: 3px 3px #999897;
    margin: 0rem 0.5rem 0rem 0.5rem;
    cursor: pointer;
}
.skill-profile img:hover{
    -webkit-filter: brightness(1.5);
            filter: brightness(1.5);
}
.skill-descrip-profile{
    grid-row: 3/5;
    font-size: 0.6rem;
    line-height: 0.8rem;
    background-color: #f3cdcd42;
    padding: 0rem 1rem 0rem 1rem;
    box-shadow: 2px 3px 4px #d3902542;
    border: 1px solid wheat;
    margin: auto 20px;
}
.ta-center{
    text-align: center;
}
.block-stat{
    grid-row: 2;
    display: grid;
    justify-items: center;
    align-items: flex-start;
    grid-template-columns: repeat(2, 1fr);
    background-color: #f3cdcd42;
    padding: 0rem 1rem 0rem 1rem;
    box-shadow: 2px 3px 4px #d3902542;
    border: 1px solid wheat;
    margin: auto 20px;
}
.first-block-stat{
    grid-column: 1;
}
.second-block-stat{
    grid-column: 2;
}
.stat-hp{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 -150px;
}
.stat-ad{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 0px;
}
.stat-velat{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 -50px;
}
.stat-ms{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 -75px;
}
.stat-reghp{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 -175px;
}
.stat-armor{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 -25px;
}
.stat-mr{
    background: url("https://lolstatic-a.akamaihd.net/frontpage/apps/prod/LolGameInfo-Harbinger/pt_BR/0d258ed5be6806b967afaf2b4b9817406912a7ac/assets/assets/images/stat-icons-sprite.png") no-repeat 0 -125px;
}
.stats{
    display: block;
    padding-left: 30px;
}
.stats-format{
    font-size: 0.8rem;
}
.stat-descrip{
    line-height: 0.5rem;
}
.champion{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.2rem;
  padding-top: 0.5rem;
  border: 1px solid #aeafae38;
  box-sizing: border-box;
  background-color: #f8e6c4b2;
  /* filter: contrast(1); */
  z-index: 0;
}

.champ-name{
  color: rgb(48, 9, 66);
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 0.7rem;
}

.champ-img{
  margin-left: 1rem;
  margin-right: 1rem;
  width: 60px;
  height: 60px;
  border: 2px solid grey;
  box-shadow: 3px 6px 10px #cececea4;
}

.champion:hover{
  color: #886A08;
  -webkit-transition: -webkit-filter .5s;
  transition: -webkit-filter .5s;
  transition: filter .5s;
  transition: filter .5s, -webkit-filter .5s;
  -webkit-filter: contrast(1.2);
          filter: contrast(1.2);
  cursor: pointer;
}
.div-main{
  background-color: #000000;
  background-image: url(https://lolstatic-a.akamaihd.net/lolkit/1.1.6/resources/images/bg-default.jpg);
  background-repeat: no-repeat;
}
.nav-ul{
  display: flex;
  justify-content: space-around;
  list-style: none;
  margin: 0;
  padding: 1rem 0rem 1rem 0rem;
  background-color: rgba(51, 51, 51, 0.438);
}
.nav-li {
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
}
.nav-li:hover {
  background-color: #111;
}
.champion-list{
    margin-top: 1rem;
    margin-left: 2rem;
    display: flex;
    flex-flow: row wrap;
    flex-direction: row;
    width: 65vw;
    padding-bottom: 6rem;
  }
  .modal-style{
    background-color: #f7f7fc;
    width: 65vw;
    height: 90vh;
    margin: auto;
    margin-top: 2%;
  }
